import React from 'react'
import { useNavigate } from 'react-router-dom'
import { Form, Input, Button, DatePicker, Picker, TextArea, Radio, Space } from 'antd-mobile';
import dayjs from 'dayjs';
// 
const moduleColumns = [
    { label: '', value: 'xingnce' },
    { label: '', value: 'shenlun' },
]

// 
const taskTypeColumns = [
    { label: '', value: 'exam' },
    { label: '', value: 'relaExam' },
    { label: '', value: 'practice' },
    { label: '', value: 'training' },
]

export default function Record() {
    const navigate = useNavigate();
    const handleFinish = (values) => {
        const data = { ...values, date: dayjs(values.date).format('YYYY-MM-DD') }
    }
    return (
        <div>
            <Form layout='horizontal'
                footer={
                    <Button block type='submit' color='primary' size='large'>
                        
                    </Button>
                } initialValues={{
                    date: new Date(),
                    module: 'xingnce',
                    taskType: 'exam',
                }} onFinish={handleFinish}>
                {/*  */}
                <Form.Item
                    name='date'
                    label=''
                    trigger='onConfirm'
                    onClick={(e, datePickerRef) => {
                        datePickerRef.current?.open()
                    }}
                >
                    <DatePicker>
                        {value =>
                            value ? dayjs(value).format('YYYY-MM-DD') : ''
                        }
                    </DatePicker>
                </Form.Item>

                {/*  */}
                <Form.Item label="" name='module'>
                    <Radio.Group>
                        <Space>
                            {
                                moduleColumns.map((item, index) => (
                                    <Radio key={index} value={item.value}>
                                        {item.label}
                                    </Radio>
                                ))
                            }
                        </Space>
                    </Radio.Group>
                </Form.Item>

                {/*  */}
                <Form.Item name='taskType' label="">
                    <Radio.Group>
                        <Space wrap>
                            {
                                taskTypeColumns.map((item, index) => (
                                    <Radio key={index} value={item.value}>
                                        {item.label}
                                    </Radio>
                                ))
                            }
                        </Space>
                    </Radio.Group>
                </Form.Item>


                <Form.Item label="" name='correct_number'>
                    <Input type='number' placeholder="" />
                </Form.Item>

                <Form.Item label="" name='total_number'>
                    <Input type='number' placeholder="" />
                </Form.Item>

                <Form.Item label="" shouldUpdate={(prev, curr) => prev.correct_number !== curr.correct_number || prev.total_number !== curr.total_number}>
                    {
                        ({ getFieldValue }) => {
                            const correct_number = getFieldValue('correct_number')
                            const total_number = getFieldValue('total_number')

                            if (!correct_number || !total_number) return ''
                            return (
                                Math.round(correct_number * 100 / total_number) + '%'
                            )
                        }
                    }
                </Form.Item>

                <Form.Item name='score' label="">
                    <Input type='number' placeholder="" />
                </Form.Item>

                <Form.Item name='remark' label="">
                    <TextArea placeholder="/" rows={3} />
                </Form.Item>
            </Form>
        </div>
    )
}